.table-container {
    width: 100%;
    overflow-x: auto;
}

table {
    /* width: 100%; */
    border-collapse: collapse;
    margin-top: 20px;
}

th,
td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

th {
    background-color: #f2f2f2;
}

td button {
    padding: 5px 10px;
    border: none;
    border-radius: 4px;
    background-color: #125875;
    color: white;
    cursor: pointer;
    margin-right: 10px;
}

td button:hover {
    background-color: #0056b3;
}

